@import url('https://fonts.googleapis.com/css2?family=Anton&family=Dancing+Script:wght@500;600&family=Poppins:wght@400;700&family=Rajdhani&family=Roboto:wght@100&display=swap');


#datatable{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
transition: 0.3s ease-in-out;    
    border-bottom: 1px solid rgb(11, 101, 107);
}

#symbol{
    font-weight: bolder;
    text-transform: uppercase;
    color: rgb(51, 49, 49);
}
#datatable:hover{
background-color: rgb(0, 6, 18);
}
#full{
    margin-bottom: 80px;
    text-align: center;
    width: 100%;
    /* margin: 10px; */
    overflow-x: scroll;
    padding: 10px;
    border-radius: 10px;
    background-color: rgb(5,3,26) ;
}
#loadmore{
    font-weight: bold;
    text-transform: uppercase;
    outline: none;
    border: none;
    /* border: 1px solid rgb(0, 157, 107); */
    color: white;
    border: 1px solid rgb(0, 157, 107);
    background-color: transparent;
    box-shadow: 2px 2px 90px 1px rgb(0, 157, 107) ;
    transition: 0.5s ease-in-out;
}
#loadmore:hover{
transform: translateX(-20px);
box-shadow: 0px 0px transparent;

    /* background-color:rgb(0, 157, 107); */

}

#navbar{
box-shadow: 1px 1px 1px #060606;
    position: fixed;
    display: block;
    background-color: #081126;
}

.navbar-brand{
    background-color: rgb(2, 38, 50);
    padding:0px 10px 0px 10px;
    border-left: 3px solid rgb(0, 191, 255);
    font-family: poppins,serif;
    border-radius: 0px 20px 20px 0px;
}

.nav-link{
    font-size: 20px;
    font-family: roboto,serif;

}

