body,html{
    overflow-x: hidden;
    background: rgb(8,18,40);
    background: linear-gradient(140deg, rgba(8,18,40,1) 0%, rgba(6,11,21,1) 48%, rgba(19,28,32,1) 100%);
  
}
#chartfirst{
    border-right: 3px solid rgb(90, 88, 88) ;
}

@media only screen and (max-width: 600px) {
    body,html{
    margin-bottom: 100px;

        overflow-x: auto;
    }
    #days{
        font-size: small;
        font-weight: lighter;
        text-transform: lowercase;
        /* font-size: 5px; */
    }
.fullchart{
    overflow-x:auto;
    
    min-width: 1000px;
}


  }
  #days:focus{
    background-color: rgb(0, 157, 107);

  }
#days:hover{
    background-color: rgb(0, 157, 107);

}
  #days{
    transition: 0.5s ease-in-out;
    text-transform: uppercase;
border:1px solid rgb(0, 157, 107);
  }



#description{
    padding: 10px;
    overflow: hidden;
    max-height: 100px;
}