
  @import url('https://fonts.googleapis.com/css2?family=Anton&family=Dancing+Script:wght@500;600&family=Poppins:wght@400;700&family=Rajdhani&family=Roboto:wght@100&display=swap');


*{
  /* overflow-y:scroll; */
  margin: 0;
  


  padding: 0;
  box-sizing: border-box;
}
body,html{
  height: 100%;
  /* background: rgb(8,18,40);
  background: linear-gradient(140deg, rgba(8,18,40,1) 0%, rgba(6,11,21,1) 48%, rgba(19,28,32,1) 100%); */

  width: 100%;
}
#first{
  
  font-size: 10px;
  margin: 10px;
  text-transform: uppercase;
  color: white;
  padding: 35px;
  border-radius: 5px;
  box-shadow: 1px 1px 1px 1px rgb(0, 157, 107);
width: 200px;
  background-color: rgb(35,38,47);
}
.home{
  overflow-x: hidden;
  height: 100%;
  width: 100%;
  background: rgb(8,18,40);
  background: linear-gradient(140deg, rgba(8,18,40,1) 0%, rgba(6,11,21,1) 48%, rgba(19,28,32,1) 100%);
}
#homerow{
  width: 100%;
  /* height: 100vh; */
}
#icon{
  animation: movericon 1s infinite  alternate;
margin-left: 12px;
  font-size: 40px;
  text-align: center;
  position: relative;
  top:120px;
}
@keyframes movericon {
  0% { transform: translateY(0); }
  100% { transform: translateY(-55px); }
}
#hometext{
  margin-top: 90px;
  position: relative;
  top:90px;
  line-height: 90px;
  font-weight: bold;
  text-align: center;
  text-transform: capitalize;
  color: white;
  
  font-size: 80px;
}
#cryptotext{
/* font-size: 70px; */
  /* text-shadow: 2px 2px 5px aqua; */
  text-transform: uppercase;
  color: rgb(54,187,145);
}
#handimg{
  margin-top: -40px;
  margin-right: -20px;
  text-align: right;
  display: flex;
  width: 600px;
  height: 400px;
/* text-align: end; */
  right: 0;
  position: relative;
}
#homeimg{
  animation: mover 1.3s infinite  alternate;
  width: 550px;
  height: 450px;
}
@keyframes mover {
  0% { transform: translateY(0px); }
  100% { transform: translateY(-30px); }
}
#first>p{
  font-size: 20px;
}
#first>img{

  width: 100px;
  height: 100px;
}
#main{
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;
  display: flex;

}
#boxrow{
  justify-content: space-evenly;

}
#box{
  justify-content: space-evenly;
  margin: 10px;
  padding: 20px;
  border-radius: 4px;
  border: 1px solid rgb(2, 44, 25);
  margin-bottom: 30px;
  background-color: rgb(5,3,26);
  width: 300px;
  height: 270px;
}
#infohead{
  font-weight: 500;
  font-family: Poppins,serif;
  font-size: 60px;
}
#iconcont{
  font-family: Poppins,serif;

}
#navbar{
  display: block;
  width: 100%;
  position:absolute;
  color: white;
 /* From https://css.glass */
background: transparent;

}